
import XImg from 'vux/src/components/x-img/index.vue'

export default {
    name:'index-noheader',
    data(){
        return{
            serverIP : this.$store.state.serverIP,
            baseList:[]
        }
    },
    components:{XImg},
    methods:{
      success (src, ele) {
        //console.log('success load', src)
        const span = ele.parentNode.querySelector('span')
        ele.parentNode.removeChild(span)
      },
      error (src, ele, msg) {
        //console.log('error load', msg, src)
        const span = ele.parentNode.querySelector('span')
        span.innerText = 'load error'
      }
    },
    mounted:function(){
      //发送ajax
      this.baseList = [
        {
          instr:'送原装壳膜耳机',
          imgUrl:'http://'+this.serverIP+'/images/57ea2b16N80a8fde1.jpg',
          name:'360手机 N5S套装版'
        },
        {
          instr:'可以拍照的电话手表',
          imgUrl:'http://'+this.serverIP+'/images/58db9174Ne76c5c3e.jpg',
          name:'360儿童手表5C'
        },
        {
          instr:'倒车影像 随手切换',
          imgUrl:'http://'+this.serverIP+'/images/571f36f6N1f0791f5.jpg',
          name:'360手机 N5S套装版'
        },
        {
          instr:'更安全的24小时保护',
          imgUrl:'http://'+this.serverIP+'/images/573545b8N8e52cb17.jpg',
          name:'360摄像机夜视plus'
        },
        {
          instr:'送原装壳膜耳机',
          imgUrl:'http://'+this.serverIP+'/images/57df55ecN32dfbf6e.jpg',
          name:'360手机 N5S套装版'
        },
        {
          instr:'可以拍照的电话手表',
          imgUrl:'http://'+this.serverIP+'/images/57ea2b16N80a8fde1.jpg',
          name:'360儿童手表5C'
        },
      ]
    }
}



